import Stars from '@/components/Common/Stars';
import RadialProgressCard from '@/components/Cards/RadialProgressCard';
import SmallLineChartCard from '@/components/Cards/SmallLineChartCard';
import AreaShadowChart from '@/components/Charts/AreaShadow';
import CommentItem from '@/components/Listing/CommentItem';
import OrderItem from '@/components/Listing/OrderItem';
import { smallChartData1, smallChartData2, smallChartData3, smallChartData4, areaChartData } from '@/data/charts';
import comments from '@/data/comments';
import orders from '@/data/orders';
export default {
  components: {
    Stars: Stars,
    RadialProgressCard: RadialProgressCard,
    SmallLineChartCard: SmallLineChartCard,
    AreaShadowChart: AreaShadowChart,
    CommentItem: CommentItem,
    OrderItem: OrderItem
  },
  data: function data() {
    return {
      isLoad: false,
      smallChartData1: smallChartData1,
      smallChartData2: smallChartData2,
      smallChartData3: smallChartData3,
      smallChartData4: smallChartData4,
      areaChartData: areaChartData,
      comments: comments.slice(0, 5),
      orders: orders
    };
  },
  methods: {},
  mounted: function mounted() {
    var _this = this;

    setTimeout(function () {
      _this.isLoad = true;
    }, 50);
  }
};